






















































































.grid {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: column wrap;
	    flex-flow: column wrap;
	width: 100%;
	height: 290vw;
	margin: 0;
}

.artist-container {
	text-align: center;
	display: inline-block;
	width: 30%;
	margin: 1.6%;
	margin-top: 10px;
	background-color: #383838;
	color: #dedede;
	vertical-align: top;
	position: relative;
}

.artist-image {
	width: 100%;
	max-height: 250px;
	overflow: hidden;
	position: relative;
}
.artist-image img {
	width: 100%;
	height: auto;
}

.artist-social {
	margin: 0 auto;
	margin-top: 10px;
	width: 60%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
}

.artist-social a {
	color: #dedede;
}

.artist-name {
	font-size: 1.8em;
}

.artist-info {
	width: 100%;
	padding: 15px;
}

.artist-player {
	width: 100%;
	background-color: #1d1717;
}

.artist-player audio {
	width: 100%;
}

.track-images {
	width: 100%;
}
.track-images img {
	float: left;
	width: 33.3%;
	display: inline-block;
	opacity: 0.7;
}
.track-images img:hover {
	opacity: 1;
	cursor: pointer;
}

.icon:hover {
	color: rgb(25,200,70);
}




