  height: 100%;
  font-size: 20px;
}

body {
  background-color: #dfdfdf;
  font-size: 14px;
  line-height: 1.4;
}

a {
  text-decoration: none;
  color: rgb(25, 200, 70);
  font-size: 1em;
}

nav {
  position: fixed;
  top: 0;
  background-color: #2b2b2b;
  width: 100%;
  padding: 10px;
  z-index: 2;
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
nav a {
  font-size: 1.5em;
  margin-left: 20px;
}

#app {
  color: #2c3e50;
  font-family: Source Sans Pro, Helvetica, sans-serif;
  margin-top: 50px;
}

h1 {
  color:rgb(25,200,70);
  text-outline: ;
}

form {
  width: 75%;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  border: 1px solid #efefef;
}

.search-input {
  -webkit-box-flex: 2;
      -ms-flex-positive: 2;
          flex-grow: 2;
  padding: 8px;
  font-size: 1.5em;
  margin-right: 0;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.search-input:focus {
  outline: none;
  outline-style: solid;
  outline-width: 1px;
  outline-color: rgb(25,70,20);
}

.submit-button {
  position: relative;
  left: -1px;
  padding: 8px;
  font-size: 1.5em;
  margin: 0;
  background-color: #2b2b2b;
  color: #727272;
  border: 1px solid #727272;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}


.search-input:focus + .submit-button {
  background-color: #1a871c;
  color: #dfdfdf;
}

.container {
  width: 60%;
}




